@import '../variables';
@import '../mixins/mixins';
.relative {
    position: relative !important;
}
.absolute {
    position: absolute !important;
}


*[dir="ltr"]{
    .right-0 {
        right: 0 !important;
    }
    .left-0 {
        left: 0 !important; 
    }
}

*[dir="rtl"]{
    .right-0 {
        left: 0 !important;
    }
    .left-0 {
        right: 0 !important; 
    }
}


.top-50 {
    top: 50% !important;
    transform: translateY(-50%);
}

.bottom-0 {
    bottom: 0 !important;
}
.bottom-5 {
    bottom: 5px !important;
}

.sb-sticky-bottom {
    position: sticky;bottom: 0;  z-index: 999999;
  }
  .sb-sticky-top {
    position: sticky;top: 0;z-index: 999999;
  }